<template>
    <!-- <yd-layout>
        <div class="mainContentFluid thirdPart">
            <div class="fluidWidth">
                <yd-flexbox direction="vertical">
                    <div class="title" v-text="title"></div>
                    <div class="btn" @click="initData()">签到</div>
                </yd-flexbox>
            </div>
        </div>
    </yd-layout> -->
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    data() {
        return {
            meetId: '',
            data: [],
            title: ''
        }
    },
    computed: {
        ...mapGetters(['getUserInfo'])
    },
    mounted() {
        document.setTitle('二维码签到');
        this.meetId = this.$route.params.meetId;
        // this.title = this.$route.params.title;
        this.initData();
    },
    methods: {
        initData: function () {
            let vm = this;
            var promise = new Promise(function (resolve, reject) {
                vm.post(vm.conf.portalMobile + '/API/MeetManager/meet/sign', {
                    'name': vm.getUserInfo.ssotoken,
                    'meetId': vm.meetId
                }, function (res) {
                    vm.$dialog.toast({
                        mes: res.errorMessage,
                        timeout: 2000
                    });
                    setTimeout(resolve(true), 2000);
                })
            });
            promise.then(function (data) {
                if (data) {
                    vm.$router.push({
                        name: 'attendee',
                        params: {
                            meetId: vm.meetId,
                            flag: false
                        }
                    })
                }
            });
        },
        getQueryString: function (name) {
            var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i');
            var r = window.location.href.substr(1).match(reg);
            if (r != null) return unescape(r[2]);
            return null;
        }
    }
}
</script>
<style scoped>
.title {
    height: 3rem;
    margin: 0 auto;
    font-size: 0.5rem;
}

.btn {
    width: 1.2rem;
    line-height: 0.5rem;
    margin: 0 auto;
    border: 1px solid #ccc;
    background-color: #ddd;
}

.btn:hover {
    background-color: #ccc;
    cursor: pointer;
}
</style>


